@font-face {
  font-family: "Avenir";
  src: url(fonts/Avenir-Book.ttf) format("truetype");
  font-weight: 300;
}

@font-face {
  font-family: "Avenir";
  src: url(fonts/Avenir-Heavy.ttf) format("truetype");
  font-weight: 600;
}

body {
  font-family: "Avenir", sans-serif;
  font-weight: 300;
  color: #3d4e61;
}

.search-bar {
  display: flex;
  justify-content: center;
  margin: 20px auto 0;
  height: 70px;
}

.search-bar--small {
  height: 42px;
}

.search-bar__label {
  position: relative;
  display: block;
  width: 100%;
}

.search-bar__label::after {
  position: absolute;
  content: "";
  display: block;
}

.search-bar__label--large:after {
  background-image: url("./images/Search.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 19px;
  height: 19px;
  top: 25px;
  left: 26px;
}

.search-bar__label--small:after {
  background-image: url("./images/Search.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 11px;
  height: 11px;
  top: 15px;
  left: 13px;
}

.search-bar__input {
  padding: 0;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  color: #3d4e61;
  font-weight: 300;
  font-family: inherit;
  box-shadow: 0 1px 8px #eee;
  outline: none;
}

.search-bar__input:hover {
  box-shadow: 0 3px 8px 0 #ccc;
}

.search-bar__input--large {
  padding-left: 62px;
  font-size: 16px;
  line-height: 22px;
}

.search-bar__input--small {
  padding-left: 33px;
  font-size: 14px;
  line-height: 19px;
}

.search-bar__input::placeholder {
  font-weight: 300;
  color: inherit;
}

.search-bar__input--large::placeholder {
  font-size: 16px;
  line-height: 22px;
}

.search-bar__input--small::placeholder {
  font-size: 14px;
  line-height: 22px;
}

.search-bar__input:focus {
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  font-weight: 600;
}
